"use client";

import Image from "next/image";
import Link from "next/link";

export default function Hero() {
  return (
    <section className="relative w-full h-[550px] bg-gray-50 overflow-hidden">
      {/* Background Image */}
      <Image
        src="/hero-banner.jpg" // ganti dengan path gambar kamu
        alt="Elzatta Hero Banner"
        fill
        priority
        className="object-cover"
      />

      {/* Overlay */}
      <div className="absolute inset-0 bg-black/20" />

      {/* Text content */}
      <div className="relative z-10 flex flex-col justify-center items-start max-w-7xl mx-auto h-full px-6">
        <div className="max-w-lg">
          <h1 className="text-4xl font-light text-white leading-tight mb-4 uppercase">
            Koleksi Terbaru <span className="font-semibold">Elzatta</span>
          </h1>
          <p className="text-white/90 text-base mb-6">
            Temukan koleksi hijab dan pakaian terbaru untuk gaya eleganmu setiap
            hari.
          </p>
          <Link
            href="/new-product"
            className="border border-white text-white px-6 py-2 uppercase tracking-wide text-sm hover:bg-white hover:text-black transition-all duration-300"
          >
            Lihat Koleksi
          </Link>
        </div>
      </div>
    </section>
  );
}
